
  /* Body */
  body {
    font-family: 'Arial', sans-serif;
    background-color: #f9f9f9;
    color: #333;
  }
  
  /* Main Container */
  .terms-container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Header */
  .terms-header {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .terms-header h1 {
    font-size: 36px;
    color: #333;
    text-transform: uppercase;
    font-weight: bold;
  }
  
  /* Section Styling */
  .terms-section {
    margin-bottom: 40px;
  }
  
  .terms-section h2 {
    font-size: 28px;
    color: #007BFF;
    margin-bottom: 15px;
  }
  
  .terms-section p {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  /* Contact Section */
  .terms-contact {
    background-color: #f1f1f1;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .terms-contact h2 {
    font-size: 24px;
    color: #007BFF;
    margin-bottom: 10px;
  }
  
  .terms-contact p {
    font-size: 16px;
    line-height: 1.6;
  }
  
  .terms-contact strong {
    color: #333;
  }
  
  /* Responsive Design */
  @media screen and (max-width: 768px) {
    .terms-container {
      padding: 20px;
    }
  
    .terms-header h1 {
      font-size: 28px;
    }
  
    .terms-section h2 {
      font-size: 24px;
    }
  
    .terms-section p,
    .terms-contact p {
      font-size: 14px;
    }
  }
  