.policy-container {
    max-width: 800px;
    background-color: #f8f9fa;
    padding: 20px;
    border-radius: 10px;
  }
  
  .policy-section h2 {
    color: #007bff;
    font-size: 1.5rem;
    border-bottom: 2px solid #007bff;
    padding-bottom: 5px;
    margin-bottom: 15px;
  }
  
  .policy-section p, .policy-section li {
    font-size: 1rem;
    color: #333;
  }
  
  .list-group-item {
    background-color: #fff;
    border: none;
    border-left: 4px solid #007bff;
    margin-bottom: 5px;
    padding: 10px;
  }
  